@import '../../../base';

.signing-container {
  .fb-button,
  .google-button {
    margin-top: 10px;
    border: none;
    background: transparent;
    width: 100%;
    height: 72px;
    border-radius: 36px;
    display: flex;
    justify-content: center;
    align-items: center;

    .signing-inner {
      margin: auto;
      display: flex;
      img {
        transform: translateY(3px);
        height: 20px;
        width: 20px;
        margin-right: 16px;
        margin-left: auto;
      }
  
      p {
        color: white;
        margin: 0;
      }
    }

    &:focus,
    &:active {
      outline: none;
    }
  }

  .fb-button {
    background-color: #3b5998;
    &:hover {
      background-color: #15264b;
      text-decoration: none;
    }
    &:active {
      background-color: #527acf;
    }
  }

  .google-button {
    background-color: #dd4b39;
    &:hover {
      background-color: #80281c;
      text-decoration: none;
    }
    &:active {
      background-color: #fd5943;
    }
  }

  .error-msg {
    color: $color-error-alt;
    font-size: $font-size-smallest;
    font-weight: $font-weight-semibold;
  }
}