@import '../../../base';

.loading-screen {
  background: linear-gradient(150.14deg, $color-input-form-landing-desc 0.6%, $color-loading 100%);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  .spinner-animation {
    top: calc(45% - 40px);
    $spinner-height: 60px;
    width: $spinner-height !important;
    height: $spinner-height !important;

    .dot1,
    .dot2 {
      width: $spinner-height * 0.5 !important;
      height: $spinner-height * 0.5 !important;
      background-color: $color-form !important;
    }
  }

  .dot1, .dot2 {
    background-color: $color-form !important;
  }

  .bottom-container {
    position: absolute;
    top: 50%;
    width: 230px;
    left: calc(50% - 115px);
    overflow-x: hidden;
    overflow-y: hidden;

    p {
      font-weight: $font-weight-bold;
      font-size: $font-size-tiny;
      color: white;
      text-align: center;
      margin: 0;
    }
  }

  .logo-container {
    display: flex;

    img {
      height: 64px;
      width: 64px;
      margin: 0 10px;
    }
  }
}

.loading-active {
  opacity: 0.9;
  transition: opacity 0.3s ease;
}

.loading-inactive {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.loading-error {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 480px;
  text-align: center;
  .error-text-main {
    font-size: $font-size-big;
    font-weight: $font-weight-bold;
    color: $color-title;
  }

  .error-text-extra {
    font-size: $font-size-regular;
    font-weight: $font-weight-semibold;
    color: $color-title;
    margin: 20px 0;
  }

  .error-text-try {
    font-size: $font-size-small;
    font-weight: $font-weight-normal;
    color: $color-caption;
    margin-bottom: 20px;
    transform: translateY(-20px);
  }
}
