@import '../../base';

.login-tab-wrapper {
  max-width: 400px;
  width: 100%;
  margin: 0 auto auto auto;
}

.login-container {
  p,
  span {
    color: $color-body;
    font-size: $font-size-regular;
    font-weight: $font-weight-normal;
  }

  margin-top: 10px;
  max-width: 400px;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  .col-12 {
    max-width: 345px;
  }

  .welcome-text {
    font-size: $font-size-big;
    font-weight: $font-weight-bold;
    color: $color-title;
  }

  .seperator-less-margin {
    margin-top: -10px !important;
  }

  .seperator {
    margin-top: 10px;
    width: 100%;
    display: flex;
    justify-content: center;
    p {
      color: $color-line;
      position: absolute;
      top: 3px;
      background-color: white;
      width: 120px;
    }

    hr {
      width: 100%;
      height: 1px;
      background-color: $color-line;
      border: none;
    }
  }

  .forget-pass {
    margin-top: -5px;
    margin-bottom: 10px;
    display: flex;
    justify-content: flex-end;

    p {
      font-size: $font-size-small;
      font-weight: $font-weight-normal;
    }
  }

  .facebook {
    color: #3b5998;
    font-weight: $font-weight-bold;
  }

  .google {
    color: #dd4b39;
    font-weight: $font-weight-bold;
  }

  .default {
    color: $color-body;
  }

  .caption {
    font-size: 13px;
    font-weight: $font-weight-normal;
  }

  .no-account {
    font-size: $font-size-regular;
    font-weight: $font-weight-normal;
  }

  .sign-up {
    font-size: $font-size-regular;
    font-weight: $font-weight-semibold;
  }

  .link-color {
    color: $color-hover;
  }

  .link-bold {
    color: $color-hover;
    font-weight: $font-weight-semibold;
  }

  .link-color,
  .link-bold {
    transition: color 0.3s ease;
    &:hover {
      color: $color-hover-alt;
      cursor: pointer;
      text-decoration: underline;
    }
  }

  .errorString {
    color: $color-error-alt;
  }

  .terms-container {
    text-align: left;
    margin-top: 17px;
    margin-bottom: 19px;
  }

  form {
    width: 100%;
    .col-12 {
      padding: 0;
    }
    .button-medium-color {
      min-width: 100% !important;
      p {
        color: white;
      }
    }
  }
}